
"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { MoreHorizontal } from "lucide-react";
import { invoices as initialInvoices } from "@/lib/data";
import { Button } from "@/components/ui/button";
import { useToast } from "@/hooks/use-toast";
import type { Invoice } from "@/lib/types";
import { InvoiceDialog } from "@/components/invoice-dialog";

const statusColors: { [key: string]: 'default' | 'secondary' | 'destructive' | 'outline' } = {
  'Paid': 'default',
  'Partial': 'secondary',
  'Due': 'outline',
  'Overdue': 'destructive'
};

export default function InvoicingPage() {
  const [invoices, setInvoices] = useState<Invoice[]>(initialInvoices);
  const [selectedInvoice, setSelectedInvoice] = useState<Invoice | undefined>(undefined);
  const [isInvoiceDialogOpen, setIsInvoiceDialogOpen] = useState(false);
  const { toast } = useToast();

  const handleRecordPayment = (invoiceId: string) => {
    setInvoices(invoices.map(inv => 
      inv.id === invoiceId ? { ...inv, status: 'Paid' } : inv
    ));
    // Also update shared data if needed
    const index = initialInvoices.findIndex(inv => inv.id === invoiceId);
    if (index !== -1) {
      initialInvoices[index].status = 'Paid';
    }
    toast({
      title: "Payment Recorded",
      description: `Payment recorded for invoice ${invoiceId}. Status updated to Paid.`,
    });
  };

  const handleSendReminder = (invoiceId: string) => {
    toast({
      title: "Action Complete",
      description: `Reminder sent for invoice ${invoiceId}.`,
    });
  };
  
  const handleViewInvoice = (invoice: Invoice) => {
    setSelectedInvoice(invoice);
    setIsInvoiceDialogOpen(true);
  };

  return (
    <>
      {selectedInvoice && (
        <InvoiceDialog 
          open={isInvoiceDialogOpen}
          onOpenChange={setIsInvoiceDialogOpen}
          invoice={selectedInvoice}
        />
      )}
      <Card>
        <CardHeader>
          <CardTitle>Invoicing</CardTitle>
          <CardDescription>Manage and track all your rental invoices.</CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Invoice ID</TableHead>
                <TableHead>Order ID</TableHead>
                <TableHead>Customer</TableHead>
                <TableHead>Due Date</TableHead>
                <TableHead>Status</TableHead>
                <TableHead className="text-right">Amount</TableHead>
                <TableHead><span className="sr-only">Actions</span></TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {invoices.map((invoice) => (
                <TableRow key={invoice.id}>
                  <TableCell className="font-mono text-xs">{invoice.id}</TableCell>
                  <TableCell className="font-mono text-xs">{invoice.orderId}</TableCell>
                  <TableCell className="font-medium">{invoice.customerName}</TableCell>
                  <TableCell>{invoice.dueDate.toLocaleDateString()}</TableCell>
                  <TableCell>
                    <Badge variant={statusColors[invoice.status]}>{invoice.status}</Badge>
                  </TableCell>
                  <TableCell className="text-right">${invoice.amount.toFixed(2)}</TableCell>
                  <TableCell>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button aria-haspopup="true" size="icon" variant="ghost">
                          <MoreHorizontal className="h-4 w-4" />
                          <span className="sr-only">Toggle menu</span>
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end">
                        <DropdownMenuItem onSelect={() => handleViewInvoice(invoice)}>View Invoice</DropdownMenuItem>
                        <DropdownMenuItem onSelect={() => handleRecordPayment(invoice.id)}>Record Payment</DropdownMenuItem>
                        <DropdownMenuItem onSelect={() => handleSendReminder(invoice.id)}>Send Reminder</DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </>
  );
}
